.InventoryItem {
  width: 100%;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #eee;
  margin: 20px 0;
  cursor: pointer;
  font-size: 1.4em;
}

li.CartItem {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  border-bottom: 1px solid #eee;
  margin: 20px 0;
  cursor: pointer;
  font-size: 1.4em;
  position: relative;
}

li.CartItem span:nth-child(2) {
  justify-self: center;
  align-self: end;
}

li.CartItem span:nth-child(3) {
  justify-self: end;
  align-self: end;
}

.CartItem__removeButton {
  width: 100%;
  height: 100%;
  background: blue;
  border: 2px solid white;
  position: absolute;
  z-index: 999;
  font-size: 0.5em;
  display: flex;
  align-items: center;
}